import MenuView from '../views/menu1/MenuView.vue'
import Menu3View from '../views/menu1/Menu3View.vue'

const routes = [
  {
    path: '/m1/menu1',
    name: 'menu1',
    component: MenuView
  },
  {
    path: '/menu2',
    name: 'menu4',
    component: () =>
      import(
        /* webpackChunkName: "menu2", webpackPrefetch:true */ '../views/menu1/Menu2View.vue'
      )
  },
  {
    path: '/menu3',
    name: 'menu3',
    component: () => import('../views/menu1/Menu3View.vue')
  },
  {
    path: '/setupview1',
    name: 'setupview1',
    component: () => import('../views/setup/SetupView.vue')
  }
]

export default routes
