import MenuView from '../views/menu2/Menu1View.vue'
import Menu3View from '../views/menu2/Menu3View.vue'

const routes = [
  {
    path: 'm2/menu1',
    name: 'menu21',
    component: MenuView
  },
  {
    path: 'menu2',
    name: 'menu2',
    component: () =>
      import(
        /* webpackChunkName: "menu2", webpackPrefetch:true */ '../views/menu1/Menu2View.vue'
      )
  },
  {
    path: 'menu3',
    name: 'menu3',
    component: () => import('../views/menu1/Menu3View.vue')
  }
]

export default routes
