export const MyVisible = {
  mounted(el, binding) {
    // console.log(el, binding)
    el.style.visibility = binding.value ? 'visible' : 'hidden'
  },
  updated(el, binding) {
    // console.log(el, binding.value)
    el.style.visibility = binding.value ? 'visible' : 'hidden'
  }
}

export const MyFocus = {
  mounted(el, binding) {
    if (el.id === binding.value) el.focus()
  },
  updated(el, binding) {
    if (el.id === binding.value) el.focus()
  }
}
